import React, { Component } from 'react';

const serviceUuid = "4fafc201-1fb5-459e-8fcc-c5c9c331914b";
const characteristicUuid = "beb5483e-36e1-4688-b7f5-ea07361b26a8";

class BLE extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            device: {name: "none"},
            connected: false,
            value: -1
        }

        this.characteristicEvent = this.characteristicEvent.bind(this);
    }

    characteristicEvent(event)
    {
        
        let value = event.target.value.getUint8(0);
        console.log("Value changed " + value);
        let newState = Object.assign({}, this.state);
        newState.value = value;
        this.setState(
            {
                value: value
            }
        )
        
    }

    async connectClick()
    {
        let device;
        let filters = [];
        let options = {};
        let characteristic;
        let connected = true;

        //options.acceptAllDevices = true;
        filters.push({namePrefix: "RoboFish"});
        options.filters = filters;
        options.optionalServices = [serviceUuid];
    
        try {
            device = await navigator.bluetooth.requestDevice(options);
    
            const server = await device.gatt.connect();
            const service = await server.getPrimaryService(serviceUuid);
            characteristic = await service.getCharacteristic(characteristicUuid);
            connected = true;

            characteristic.addEventListener("characteristicvaluechanged", this.characteristicEvent);
            characteristic.startNotifications();
        }
        catch (error) {
            console.log('Argh! ' + error);
            connected = false;
        }

        if(device === undefined)
        {
            device = {name: "none"};
            connected = false;
        }

        this.setState(
            {
                device: device,
                characteristic: characteristic,
                connected: connected
            }
        )
    
    }

    async toggleClick()
    {
        let device = this.state.device;
        let characteristic = this.state.characteristic;

        if(device.name !== "none")
        {
            let encoder = new TextEncoder('utf-8');
            let sendMsg = encoder.encode("blue");
            characteristic.writeValue(sendMsg);
       }
    }

    render()
    {
        let hide = {
            display: this.state.connected ? 'block' : 'none'
        }
        return (
            <div>
                <h1>SwimBot 2.2</h1>
                <button onClick={() => this.connectClick()}>Connect</button>
                <div style={hide}>
                    <h2>Connected to : {this.state.device.name}</h2>
                    <button onClick={() => this.toggleClick()}>Toggle led</button>
                    <div><p>Value received: {this.state.value}</p></div>
                </div>
            </div>
        );
    }

}

export default BLE;